body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.app {
  width: 100%;
  height: 100%;
  position: relative; }

.page-header, .editor-header { height: 2em }

.page-footer { height: 3em }

.editor-pane {
  height: 25em;
  width: 50%;
  position: relative;
}

.editor-header {
    width: 50%;
    position: relative;
}

.editor-wrapper {
  display: -webkit-flex;
  display: flex;
}
.change-marker {
  position: absolute;
}

.reset-wrapper {
  position: relative;
}
.reset-code {
  position: absolute;
  top: -2em;
  z-index: 4;
}

.danger {
  background: hsl(348, 100%, 61%);
}

.warning {
  background: hsl(48, 100%, 67%);
}

.success {
  background: hsl(141, 53%, 53%);
}

.progressbar-wrapper {
  height: 2em;
  width: 100%;
  border-radius: 5px;
  border: 2px solid #212121;
}

.progressbar-filler {
  height: 100%;
  border-radius: inherit;
  transition: width .2s ease-in;
}
